@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;900&display=swap);


.upload-input{display:flex;flex-direction:column;align-items:center}

.uploaded-img{width:640px;height:480px;background-repeat:no-repeat;background-size:cover;display:grid;grid-template-columns:1fr 1fr 1fr 1fr 1fr 1fr;cursor:pointer}.uploaded-img .div-area{height:100%;width:100%;border:solid 1px white}.uploaded-img .div-area.selected{opacity:0.3;background-color:black}

.description{display:flex;flex-direction:column;box-shadow:black;justify-content:space-around;padding:1.875rem;background-color:white;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);margin-top:50px}.description .title{border:none;font-weight:900;font-size:1.125rem;line-height:1.5;outline:none}.description .desc-area{border:none;line-height:1.7;font-size:0.875rem;resize:none;outline:none}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 10% auto;
}

.background-img {
  width: 480px;
  height: 360px;
}

* {
  font-family: 'Roboto', sans-serif;
}
